import React, { useEffect } from "react";
import styles from "./MenuList.module.scss";
import { Box, Container, Grid, Typography } from "@mui/material";
import { Link, Navigate } from "react-router-dom";
import caphe from "../img/highlansPHIN_SUA_DA_5.1.png";
import freeze from "../img/FREEZE-TRA-XANH-5.1.png";
import Tea from "../img/TRA-SEN-VANG-CN-5.1.png";
import AOS from "aos";
import "aos/dist/aos.css";

function MenuList(props) {
    // useEffect
    useEffect(() => {
        setTimeout(() => {
            AOS.init();
            AOS.refresh();
        }, 300);
    }, []);
    // ------------
    return (
        <Container className={styles.container}>
            <div className={styles.wrapItem}>
                <Grid container className={styles.wrapMenu}>
                    <Grid
                        item
                        xs={7}
                        className={[styles.Menucontent, styles.MenuCoffee].join(
                            " "
                        )}
                    >
                        <Typography variant="h3">
                            <Link to="">c ph</Link>
                        </Typography>
                        <Typography
                            variant="body1"
                            className={styles.MenuParagrap}
                            data-aos="fade-up"
                            data-aos-duration="2000"
                        >
                            S kt hp hon ho gia ht c ph Robusta &amp;
                            Arabica thng hng c trng trn nhng vng cao
                            nguyn Vit Nam mu m, qua nhng b quyt rang xay
                            c o, Highlands Coffee chng ti t ho gii
                            thiu nhng dng sn phm C ph mang hng v m
                             v tinh t.
                        </Typography>

                        <span
                            className={styles.wrapLink}
                            data-aos="fade-zoom-in"
                            data-aos-easing="ease-in-back"
                            data-aos-delay="500"
                            data-aos-offset="0"
                        >
                            <Link to="/ca-phe.html">khm ph thm</Link>
                        </span>
                    </Grid>

                    <Grid item xs={5} className={styles.Menumedia}>
                        <img src={caphe} alt="imgCaPhe" />
                    </Grid>
                </Grid>

                <Grid container className={[styles.freezeMenu].join("")}>
                    <Grid item xs={5} className={[styles.freeze].join("")}>
                        <img src={freeze} alt="imgfreeze" />
                    </Grid>

                    <Grid
                        item
                        xs={7}
                        className={[styles.Menucontent, styles.MenuCoffee].join(
                            " "
                        )}
                    >
                        <Typography variant="h3">
                            <Link to="">freeze</Link>
                        </Typography>
                        <Typography
                            variant="body1"
                            className={[styles.freezeParagrap].join("")}
                            data-aos="fade-up"
                            data-aos-duration="2000"
                        >
                            Sng khoi vi thc ung  xay phong cch Vit.
                            Freeze l thc ung  xay mt lnh c pha ch t
                            nhng nguyn liu thun ty ca Vit Nam.
                        </Typography>

                        <span
                            className={styles.wrapLink}
                            data-aos="fade-zoom-in"
                            data-aos-easing="ease-in-back"
                            data-aos-delay="500"
                            data-aos-offset="0"
                        >
                            <Link to="/freeze.html">khm ph thm</Link>
                        </span>
                    </Grid>
                </Grid>

                <Grid container className={[styles.tea].join("")}>
                    <Grid item xs={7} className={[styles.MenuTea].join()}>
                        <Typography variant="h3" className={styles.titleTea}>
                            <Link to="">tr</Link>
                        </Typography>
                        <Typography
                            variant="body1"
                            className={styles.MenuParagrap}
                            data-aos="fade-up"
                            data-aos-duration="2000"
                        >
                            Hng v t nhin, thm ngon ca Tr Vit
                            vi&nbsp;phong cch hin i ti&nbsp;Highlands
                            Coffee s gip bn gi m v gic ca bn thn v
                            tn hng mt cm gic tht khoan khoi, ti mi.
                        </Typography>

                        <span
                            className={styles.wrapLink}
                            data-aos="fade-zoom-in"
                            data-aos-easing="ease-in-back"
                            data-aos-delay="500"
                            data-aos-offset="0"
                        >
                            <Link to="/tea.html">khm ph thm</Link>
                        </span>
                    </Grid>

                    <Grid
                        item
                        xs={5}
                        className={[styles.Menumedia, styles.TeaMedia].join("")}
                    >
                        <img src={Tea} alt="imgtea" />
                    </Grid>
                </Grid>
            </div>
        </Container>
    );
}

export default MenuList;
