import React from "react";
import { Link, useParams } from "react-router-dom";
import { coffeeProduct } from "./Sinhto.jsx";
import styles from "./Detail.module.scss";

import { Button, Container, Typography } from "@mui/material";

import { ToastContainer, toast } from "react-toastify";
import { useCart } from "./context/Shop_context.jsx";

function DetailSinhto({ match }) {
    const { addtocart } = useCart();

    const { id } = useParams();
    const product = coffeeProduct.find((p) => p.id === parseInt(id));

    // toastify
    const notify = () =>
        toast.success(`Bn  chn sn phm ${product.name} thnh cng!`);

    return (
        <div>
            <Container>
                <h2 className={styles.titlelistCoffee}>{product.name}</h2>
                <div className={styles.desclistCoffee}>
                    <img src={product.img} alt="" className={styles.thumb} />
                    <div className={styles.content}>
                        <span>
                            <Typography variant="body1" className={styles.text}>
                                {product.content}
                            </Typography>
                        </span>

                        <Button
                            onClick={() => {
                                notify();
                                addtocart(product);
                            }}
                        >
                            <Link to="/gio-hang.html">t mua ngay</Link>
                        </Button>

                        <span className={styles.size}>
                            <p>Size: </p>
                            <button>S</button>
                        </span>

                        <span className={styles.price}>
                            <p>Gi: </p>
                            <b>{product.price},000 VN</b>
                        </span>
                    </div>
                </div>
            </Container>
        </div>
    );
}

export default DetailSinhto;
